@keyframes liveBg {
  0% {
    background-position: 0 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0 50%;
  }
}
.bg-fancy {
  background: linear-gradient(45deg, #da46ae, #4285F4);
}

.bg-fancy-live {
  background: linear-gradient(45deg, #da46ae, #4285F4, #da46ae, #4285F4);
  background-size: 400% 400%;
  animation: liveBg 15s ease-in-out infinite;
}

.form-control {
  height: unset;
}

.has-main {
  background: rgba(0, 0, 0, 0.05);
}

.breadcrumb {
  background: none;
  font-size: 0.9em;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.card .card-header {
  padding: 0.75rem;
  border-bottom: 0;
  background: transparent;
  color: #4285f4;
}
.card .card-header h4 {
  font-size: 1.25rem;
}
.card .card-body {
  padding: 0.75rem;
}